

import React ,{useContext}from 'react';
import { TodoContext } from "../context/TodoProvider";

function Todo(props) {
    const todoContext=useContext(TodoContext);
    const { deleteTodo, setTodo } = todoContext;
    return (
        <div>
           <li>
                <span onClick={() => setTodo(props.todo)} className="material-icons">edit</span>

                <span className={`${props.todo.completed ?`completed` : null }`}>{props.todo.title}</span>

                <span className="material-icons" onClick={() => deleteTodo(props.todo.id)}>delete_outline</span>
           </li> 
        </div>
    )
}

export default Todo




