

import {GET_TODOS, SET_TODO,  CREATE_TODO, UPDATE_TODO, DELETE_TODO} from './constant';
import React, {createContext, useReducer} from 'react';
export const TodoContext = createContext();

// Initial state 
 const TodoProvider = (props) =>{
  
      const initialState={
                    todos:[{
                        "userId": 1,
                        "id": 1,
                        "title": "delectus aut autem",
                        "completed": false
                      },
                      {
                        "userId": 1,
                        "id": 2,
                        "title": "quis ut nam facilis et officia qui",
                        "completed": false
                      },
                      {
                        "userId": 1,
                        "id": 3,
                        "title": "fugiat veniam minus",
                        "completed": false
                      },
                      {
                        "userId": 1,
                        "id": 4,
                        "title": "et porro tempora",
                        "completed": true
        
      
                          
                      
                      }],
      currentTodo : null,
  }

  // Action
const createTodo = todo =>{
  dispatch ({
    type : CREATE_TODO,
    payload : todo
   
  })
}


const setTodo = todo =>{
  dispatch ({
    type : SET_TODO,
    payload : todo
   
  })
}

const updateTodo = todo =>{
  dispatch ({
    type : UPDATE_TODO,
    payload : todo
   
  })
}

const deleteTodo = id =>{
  dispatch ({
    type : DELETE_TODO,
    payload : id
   
  })
}
  // Reducer
const todoReducer = (state, action) => {
  switch(action.type){

    case CREATE_TODO:
      return {
        ...state,
        todos: [action.payload,...state.todos]
      };

      case SET_TODO:
      return {
        ...state,
        currentTodo : action.payload
      };

      case UPDATE_TODO:
      return {
        ...state,
        todos : state.todos.map(todo => todo.id === action.payload.id ? action.payload:todo)
      }

      case DELETE_TODO:
      return {
        ...state,
        todos : state.todos.filter(todo => todo.id !== action.payload)
      };

  default :
      return state;
  }
};

const [state, dispatch] = useReducer(todoReducer, initialState );

  return (
    <TodoContext.Provider 
    value={{ 
              todos :state.todos,
              currentTodo :state.currentTodo,
              createTodo,
              deleteTodo,
              setTodo,
              updateTodo  
          }}>

      {props.children}

    </TodoContext.Provider>
  )
}

export default TodoProvider;
